import React, {Component, useState, useEffect, useLayoutEffect} from 'react';
import Typewritter from "../components/Typewritter";

const Header = () => {

    return (
        <div className="Header">
            <section id="home">
                <header className="header">
                    <div className="header__art-main" />
                    <div className="header__element-dots " style={{ bottom: '20px', left: '20px' }} />
                    <div className="header__element-dots header__rotate-90" style={{ top: '40px', right: '50px' }} />
                    <div className="header__element-circle-one" style={{ top: '400px', right: '500px' }} />
                    <div className="header__element-circle-two" style={{ top: '150px', left: '250px' }} />
                    <div className="header__element-cross" style={{ top: '150px', left: '100px' }} />
                    <div className="header__element-cross" style={{ top: '120px', left: '70px' }} />
                    <div className="header__element-cross" style={{ top: '90px', left: '40px' }} />
                    <div className="header__element-box-two" style={{ top: '90px', left: '200px' }} />

                    <div className="header__element-dots header__middle-element" style={{ top: '20px', left: '50%', transform: 'translate(-50%, 0)' }} />
                    <div className="header__element-dots header__middle-element header__rotate-90" style={{ bottom: '-50px', left: '40%', transform: 'translate(-50%, 0)' }} />
                    <div className="header__element-circle-one header__middle-element" style={{ bottom: '100px', right: '50%' }} />
                    <div className="header__element-box-one header__middle-element" style={{ bottom: '50%', left: '50%', transform: 'translate(-50%, -50%)' }} />
                    <div className="header__element-box-two header__middle-element" style={{ bottom: '40%', left: '40%', transform: 'translate(-50%, -50%)' }} />
                    <div className="header__element-cross header__rotate-45 header__middle-element" style={{ bottom: '150px', left: '400px' }} />

                    <div className="header__content">
                        <h1 className="header__content--title"><Typewritter inputStrings={["I am Sholomon Pinoliad"]} /></h1>
                        <p className="header__content--subtitle">Motivated. Outgoing. Confident</p>

                        <div className="header__content--button-wrapper">
                            <div className="header__content--button-wrapper__item">
                                <div className="header__content--button-wrapper__item--center">
                                    <a href="#contact" className="header__content--button-wrapper__item--center__first-link">Hire Me!</a>
                                </div>
                            </div>
                            <div className="header__content--button-wrapper__item">
                                <div className="header__content--button-wrapper__item--center">
                                    <a href="#about" className="header__content--button-wrapper__item--center--second-link">Why Hire Me?</a>
                                </div>
                            </div>
                        </div>
                    </div>
                </header>
            </section>
        </div>
    )
}

export default Header;
