import React, {Component} from "react";

export default class Typewritter extends Component {
    state = {
        startingText: "Hello there Traveler!",
        texts: {
            1: {
                text: "I am Sholomon Pinoliad..."
            },
            2: {
                text: "How are you?",
            },
            3: {
                text: "Checking my works?",
            },
            4: {
                text: "I hope you liked it!",
            },
            5: {
                text: "You can reach me anytime",
            },
            6: {
                text: "Thank you!!!",
            },
            7: {
                text: "...",
            },
            8: {
                text: "You still there?",
            },
            9: {
                text: "Awesome!",
            },
        },
        typed: "",
        number: 1,
    };

    componentDidMount() {
        setTimeout(() => {
            this.typewriter(
                this.state.startingText
            );
        }, 1500)
    }

    typewriter(string) {
        if (string.length === 0) {
            if(this.state.typed.length !== 0) {
                setTimeout(() => this.removal(this.state.typed), 1500)
            } else {
                if (this.state.number !== 10) {
                    this.setState((state) => (
                        state.typed = ""
                    ))
                    this.typewriter(this.state.texts[this.state.number].text)
                    this.setState((state) => ({
                        number: state.number + 1
                    }))

                } else {
                    this.setState((state) => (
                        state.typed = ""
                    ))
                    this.typewriter(this.state.startingText)
                    this.setState((state) => ({
                        number: 1
                    }))
                }
            }
        } else {
            this.setState((state, props) => ({
                typed: state.typed.concat(string[0])
            }))

            setTimeout(() => this.typewriter(string.slice(1)), 150)
        }
    }

    removal(string) {
        if(string.length !== 0) {
            string = string.slice(0,-1)
            this.setState((state, props) => ({
                typed: string
            }))
            setTimeout(() => this.removal(string), 50)
        } else {
            setTimeout(() => this.typewriter(""), 1500)
        }
    }

    render() {
        return <div className="typewritter">{this.state.typed}</div>
    }
}