import React, {Component} from "react";
import wp from '../contents/wordpress.json';

const Wordpress = () => {
    return (
        <div className="wrapper__grid grid-column-two p-50">
            {
                wp.map( (wordpress) => {
                    return (
                        <div className="wrapper__grid--content p-20" key={ wordpress.id }>
                            <div className="wrapper__grid--content__image" style={{ backgroundImage: `url( ${ wordpress.image })` }}>
                                <div className="wrapper__grid--content__image--shadow">
                                    <div className="wrapper__grid--content__image--shadow__content">
                                        <div className="wrapper__grid--content__image--shadow__content--title">{ wordpress.title }</div>
                                        <p className="wrapper__grid--content__image--shadow__content--description">{ wordpress.description }</p>
                                        <p>
                                            {
                                                wordpress.tags.map( (tag) => {
                                                    return (
                                                        <span className="wrapper__grid--content__image--shadow__content--tag">{tag}</span>
                                                    )
                                                })
                                            }
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    )
                })
            }
        </div>
    )
}

export default Wordpress