import React, {Component} from "react";
import illustrations from '../contents/illustrations.json';

const Illustrations = () => {
    return (
        <div className="wrapper__grid grid-column-three p-50">
            {
                illustrations.map( (illustration) => {
                    return (
                        <div className="wrapper__grid--content p-20" key={ illustration.id }>
                            <div className="wrapper__grid--content__image-auto">
                                <img src={ illustration.image } alt=""/>
                            </div>
                        </div>
                    )
                })
            }
        </div>
    )
}

export default Illustrations