import React, {Component} from "react";
import videos from '../contents/video.json';

const Videos = () => {
    return (
        <div className="wrapper__grid grid-column-two p-50">
            {
                videos.map( (video) => {
                    return (
                        <div className="wrapper__grid--content p-20" key={ video.id }>
                            <div className="wrapper__grid--content__image-auto">
                                <video width="100%" height="auto" controls>
                                    <source src={ video.link } type="video/mp4" />
                                    Your browser does not support the video tag.
                                </video>
                            </div>
                        </div>
                    )
                })
            }
        </div>
    )
}

export default Videos