import React, {Component} from "react";
import data from '../contents/creatives.json';

const Creatives = () => {
    return (
        <div className="wrapper__grid grid-column-two p-50">
            {
                data.map( (data) => {
                    return (
                        <div className="wrapper__grid--content p-20" key={ data.id }>
                            <div className="wrapper__grid--content__image" style={{ backgroundImage: `url( ${ data.image })` }}>
                                <div className="wrapper__grid--content__image--shadow">
                                    <div className="wrapper__grid--content__image--shadow__content">
                                        <div className="wrapper__grid--content__image--shadow__content--title">{ data.title }</div>
                                        <p className="wrapper__grid--content__image--shadow__content--description">{ data.description }</p>
                                        <p>
                                            {
                                                data.tags.map( (tag) => {
                                                    return (
                                                        <span className="wrapper__grid--content__image--shadow__content--tag">{tag}</span>
                                                    )
                                                })
                                            }
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    )
                })
            }
        </div>
    )
}

export default Creatives